<template>
  <div>
    <div class="wrapper">
      <div class="sample">
        <!-- <i class="fas fa-exclamation-circle"></i>
        <i class="fas fa-check-circle"></i>-->
        <app-progress :progressWidth="progressWidth"></app-progress>
        <form @submit.prevent="formSubmited = true">
          <app-input
            v-for="(item, index) in inputs"
            :key="index"
            :name="item.name"
            :value="item.value"
            :pattern="item.pattern"
            @changedata="onChange(index, $event)"
          ></app-input>
          <button
            :disabled="done !== inputs.length"
            type="submit"
            @click="submited"
            class="btn btn-primary"
          >
            {{form}}
            <i v-if="form == 'loading'" class="fas fa-spinner"></i>
          </button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import AppInput from "./Input";
import AppProgress from "./Progress";
import AppResult from "./Result";

import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      done: 0,
      controls: []
    };
  },
  created() {
    for (let i = 0; i < this.inputs.length; i++) {
      this.controls.push(false);
    }
    // console.log(this.inputs);
  },
  computed: {
    ...mapGetters(["inputs", "form"]),
    progressWidth() {
      return {
        width: (100 / this.inputs.length) * this.done + "%"
      };
    }
  },
  methods: {
    onChange(index, e) {
      this.$store.commit("changeValue", {
        index: index,
        value: e.value
      });
      this.controls[index] = e.valid;
      let done = 0;
      for (let i = 0; i < this.controls.length; i++) {
        if (this.controls[i]) {
          done++;
        }
      }
      // console.log(done, this.inputs.length);
      this.done = done;
      // console.log(index, e.value, e.valid);
    },
    submited() {
      this.$store.dispatch("sendForm");
    }
  },
  components: {
    AppInput,
    AppProgress,
    AppResult
  }
};
</script>
<style scoped>
.fa-spinner {
  animation: flip 1s linear infinite;
}
@keyframes flip {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>


