import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    inputs: [
      {
        name: "Name",
        value: "",
        pattern: /^[a-zA-Z]{2,30}$/
      },
      {
        name: "Phone",
        value: "",
        pattern: /^[0-9]{7,14}$/
      },
      {
        name: "Email",
        value: "",
        pattern: /.+@.+\..+/
      },
      {
        name: "Some Field 1",
        value: "",
        pattern: /.+/
      },
      {
        name: "Some Field 2",
        value: "",
        pattern: /.+/
      }
    ],
    form: 'Send Data'
  }, getters: {
    name(state) {
      return state.inputs[0].value;
    },
    inputs(state) {
      return state.inputs;
    },
    form(state) {
      return state.form;
    }
  },
  mutations: {
    changeValue(state, payload) {
      state.inputs[payload.index].value = payload.value;
    },
    formSend(state) {
      state.form = 'loading';
    },
    formDone(state) {
      state.form = 'done';
    }
  },
  actions: {
    sendForm(store) {
      store.commit('formSend');

      setTimeout(() => {
        store.commit('formDone')
      }, 1000);
    }
  },
  strict: process.env.NODE_ENV !== 'production'
});


