<template>
  <header>
    <div class="container">
      <div class="row">
        <div class="col col-sm-12">
          <h1>Site</h1>
          <h2 v-if="form == 'done'">Hello, {{ name }}</h2>
        </div>
      </div>
    </div>
    <hr />
  </header>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["name", "form"])
  }
};
</script>



