<template>
  <div class="progress">
    <div
      class="progress-bar"
      role="progressbar"
      aria-valuenow="25"
      aria-valuemin="0"
      aria-valuemax="100"
      :style="progressWidth"
    ></div>
  </div>
</template>
<script>
// :style="progressWidth"
export default {
  props: ["progressWidth"],
  computed: {}
};
</script>


