<template>
  <section>
    <div class="container">
      <div class="row">
        <div class="col col-sm-6">
          <app-form v-if="form !== 'done' "></app-form>
          <app-result v-else></app-result>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import AppForm from "./Form";
import AppResult from "./Result";
import { mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {},
  computed: {
    ...mapGetters(["form"])
  },
  components: {
    AppForm,
    AppResult
  }
};
</script>


