<template>
  <table class="table table-bordered">
    <tr v-for="(item,index) in inputs" :key="index">
      <td>{{item.name}}</td>
      <td>{{item.value}}</td>
    </tr>
  </table>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["inputs"])
  }
};
</script>


